"use client";

import Image from "next/image";
import { useEffect, useMemo, useState } from "react";
import { galleryImages } from "../data/gallery";

const filterOptions = ["All", "Furniture", "Automotive", "RV & Marine", "Commercial"];

export default function Gallery() {
  const [activeCategory, setActiveCategory] = useState(filterOptions[0]);
  const [lightboxImage, setLightboxImage] = useState(null);

  const visibleImages = useMemo(() => {
    if (activeCategory === "All") {
      return galleryImages;
    }
    return galleryImages.filter((image) => image.category === activeCategory);
  }, [activeCategory]);

  useEffect(() => {
    if (!lightboxImage) {
      return undefined;
    }

    const handleKeyUp = (event) => {
      if (event.key === "Escape") {
        setLightboxImage(null);
      }
    };

    const previousOverflow = document.body.style.overflow;
    document.body.style.overflow = "hidden";
    window.addEventListener("keyup", handleKeyUp);

    return () => {
      document.body.style.overflow = previousOverflow;
      window.removeEventListener("keyup", handleKeyUp);
    };
  }, [lightboxImage]);

  return (
    <section id="gallery" className="section gallery-section">
      <h2 className="section-title">Gallery</h2>
      <p className="section-description">
        Curated highlights from automotive, furniture, marine, and commercial projects. Tap any
        preview to see a larger finish.
      </p>
      <div className="gallery-toolbar">
        <div className="gallery-filter-group">
          {filterOptions.map((option) => (
            <button
              key={option}
              type="button"
              className={`gallery-filter ${activeCategory === option ? "active" : ""}`}
              onClick={() => setActiveCategory(option)}
            >
              {option}
            </button>
          ))}
        </div>
        <p className="gallery-count">
          {visibleImages.length} {visibleImages.length === 1 ? "photo" : "photos"}
        </p>
      </div>
      <div className="gallery-grid">
        {visibleImages.map((image) => (
          <figure key={image.src} className="gallery-item">
            <button
              type="button"
              className="gallery-trigger"
              onClick={() => setLightboxImage(image)}
              aria-label={`Open ${image.category} image`}
            >
              <Image
                src={image.src}
                alt={image.alt}
                width={400}
                height={280}
                className="gallery-thumb"
                loading="lazy"
              />
            </button>
            <figcaption className="gallery-caption">{image.category}</figcaption>
          </figure>
        ))}
      </div>

      {lightboxImage && (
        <div className="gallery-lightbox" role="presentation" onClick={() => setLightboxImage(null)}>
          <div
            className="gallery-lightbox-inner"
            onClick={(event) => event.stopPropagation()}
            role="dialog"
            aria-modal="true"
          >
            <button
              type="button"
              className="gallery-lightbox-close"
              onClick={() => setLightboxImage(null)}
              aria-label="Close preview"
            >
              &times;
            </button>
            <Image
              src={lightboxImage.src}
              alt={lightboxImage.alt}
              width={1000}
              height={650}
              className="gallery-lightbox-image"
              priority
            />
            <p className="gallery-lightbox-meta">{lightboxImage.alt}</p>
          </div>
        </div>
      )}
    </section>
  );
}

